@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@300&display=swap);
@import url(https://fonts.cdnfonts.com/css/d3-euronism);
:root {
    --primary-white: #ffffff;
    --primary-light-grey: #f4f4f4;
    --primary-grey: #ccd1d9;
    --primary-dark-grey: #666666;
    --primary-orange: #f9ab00;
    --primary-dark: rgb(52, 58, 64);
    --primary-red: #d3443d;
    --primary-hover-red: #d3443d;
    --primary-black: #000000;
    --primary-transparent-black: rgba(0, 0, 0, 0.7);
    --primary-green: green;
}

body {
    font-family: 'Roboto Condensed', sans-serif !important;

}

nav {
    z-index: 2;
}

.logo {
    width: 16.25rem;
}

/*----- MENU LINKS --------*/
nav a.nav-link {
    font-size: 0.8rem;
    letter-spacing: 0.0375rem;
    cursor: pointer;
}

nav ul li {
    text-transform: uppercase;
}

.navbar-nav > li > a {
    margin-right: 0.75rem;
    margin-left: 0.75rem;
    color: #ffffff !important;
    color: var(--primary-white) !important;
    border-bottom: 0.1875rem solid rgb(52, 58, 64);
    border-bottom: 0.1875rem solid var(--primary-dark);
}

.navbar-brand {
    max-width: 17.25rem;
    padding: 0 !important;
    height: 6rem !important;
}

@media (max-width: 768px) {
    .navbar-brand {
        width: 7rem;
    }
}

.navbar-nav > li > a.active,
.navbar-nav > li > a:focus {
    border-bottom: 0.1875rem solid #f9ab00;
    border-bottom: 0.1875rem solid var(--primary-orange);
}

.navbar-nav > li > a:hover {
    border-bottom: 0.1875rem solid #f9ab00;
    border-bottom: 0.1875rem solid var(--primary-orange);
}

.header-wraper {
    position: relative;
    background: url(/static/media/webdev-bg-screen.13b8e993.jpg) no-repeat;
    background-size: cover;
    background-position: center;
    height: 100vh;
}

.main-info {
    display: flex;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    z-index: 1;
}

.main-info h1 {
    font-family: 'D3 Euronism', sans-serif;
    font-size: 3.5rem;
    color: #f9ab00;
    color: var(--primary-orange);
    text-transform: uppercase;
}

.navbarMain {
    height: 5rem;
    background-color: rgba(0, 0, 0, 0.5);
}

.tiped-text {
    font-family: 'D3 Euronism', sans-serif;
    font-size: 1.5rem;
}

.social-networks {
    padding-left: 0;
}

.social-networks a {
    display: block;
    position: relative;
    margin: 0;
    width: 45px;
    height: 45px;
    line-height: 0;
    text-align: center;
    text-decoration: none;
    color: #f9ab00;
    color: var(--primary-orange);
}

.social-networks a:hover {
    color: #d3443d;
    color: var(--primary-hover-red);
}

.social-networks li {
    display: inline-block;
}

/*-------- TYPED TEXT ---------------*/
.typed-text {
    font-size: 2rem;
    color: #ffffff;
    color: var(--primary-white);
}

@media (max-width: 768px) {
    .main-info h1 {
        font-size: 1.7rem !important;
    }
    .typed-text {
        font-size: 1.3rem !important;
    }
}

.btn-main-offer {
    border: 1px solid #d3443d;
    border: 1px solid var(--primary-red);
    text-transform: uppercase;
    border-radius: 0;
    padding: 0.625rem 1.75rem 0.75rem 1.75rem;
    color: #ffffff;
    color: var(--primary-white);
    margin: 2rem;
}

.btn-main-offer:hover {
    text-decoration: none;
    background-color: #d3443d;
    background-color: var(--primary-hover-red);
    color: #ffffff;
    color: var(--primary-white);
    transition: 0.2s ease-in-out;
}

/*-------- PARTICLES JS ---------------*/
.tsparticles-canvas-el {
    position: absolute;
    height: 100vh;
    width: 100vw;
    z-index: 1;
}

@media (max-width: 768px) {
    .particles-canvas {
        display: none;
    }
}

.photo-wrap {
    display: flex;
    justify-content: center;
    align-items: center;
}

#about h1 {
    text-transform: uppercase;
    color: #f9ab00;
    color: var(--primary-orange);
    text-align: center;
}

.polaroid img {
    display: block;
    max-width: 200px;
}

.polaroid span {
    background: #ffffff;
    display: inline-block;
    margin: 0px 75px 30px;
    padding: 15px 15px 30px;
    text-align: center;
    text-decoration: none;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.3);
    transition: all 0.2s linear;
    z-index: 0;
    position: relative;
}

.link-href {
    color: black;
}

.font-trebuchet {
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande',
        'Lucida Sans', Arial, sans-serif;
}

.card {
    position: relative;
    display: flex;
    flex-direction: column;
    min-width: 0;
    background-color: #fff;
    background-clip: border-box;
    border: 1px solid rgba(0, 0, 0, 0.125);
    border-radius: 0.25rem;
    color: #666666;
    color: var(--primary-dark-grey);
}

.card-header:first-child {
    border-radius: calc(0.25rem - 1px) calc(0.25rem - 1px) 0 0;
}

.card-header {
    padding: 0.75rem 1.25rem;
    margin-bottom: 0;
    background-color: rgba(0, 0, 0, 0.03);
    border-bottom: 1px solid rgba(0, 0, 0, 0.125);
}

@media (max-width: 468px) {
    .profile-img {
        width: 10rem;
    }
}

.services {
    text-align: center;
    padding-bottom: 2rem;
    background: #f4f4f4;
    background: var(--primary-light-grey);
}

.services h1 {
    color: rgb(52, 58, 64);
    color: var(--primary-dark);
    text-transform: uppercase;
}

.services .circle {
    position: relative;
    margin: 0.375rem auto;
    background: #f9ab00;
    background: var(--primary-orange);
    border-radius: 50%;
    width: 3.125rem;
    height: 3.125rem;
}

.services .icon {
    font-size: 1.4rem;
    color: #ffffff;
    color: var(--primary-white);
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.services .box {
    height: 16rem;
    border-bottom: 0.3125rem solid #f9ab00;
    border-bottom: 0.3125rem solid var(--primary-orange);
    padding: 0.625rem;
    margin-bottom: 1.875rem;
    background: #ffffff;
    background: var(--primary-white);
    transition: 0.3s ease-in-out;
}

.services .box:hover {
    background: #f9ab00;
    background: var(--primary-orange);
    border-bottom: 0.3125rem solid rgb(52, 58, 64);
    border-bottom: 0.3125rem solid var(--primary-dark);
}

.services .box:hover .icon {
    color: rgb(52, 58, 64);
    color: var(--primary-dark);
}

.services .box:hover .circle {
    background: #ffffff;
    background: var(--primary-white);
}

.services .box:hover p {
    color: #ffffff;
    color: var(--primary-white);
}

.experience {
    padding: 0.7rem;
}

.experience-wrapper {
    position: relative;
    padding: 3.125rem 0;
    overflow: hidden;
}

.experience h1 {
    text-transform: uppercase;
    color: #f9ab00;
    color: var(--primary-orange);
}

.experience-wrapper::before {
    position: absolute;
    content: '';
    top: 0;
    left: 50%;
    margin-left: -0.0625rem;
    width: 0.125rem;
    height: 100%;
    background: #ccd1d9;
    background: var(--primary-grey);
    z-index: 1;
}

.timeline-block {
    width: calc(50% + 0.5rem);
    display: flex;
    justify-content: space-between;
    clear: both;
}

.timeline-block-right {
    float: right;
}

.timeline-block-left {
    float: left;
    direction: rtl;
}

.marker {
    width: 1rem;
    height: 1rem;
    border-radius: 50%;
    border: 0.3125rem solid rgb(52, 58, 64);
    border: 0.3125rem solid var(--primary-dark);
    background: #f9ab00;
    background: var(--primary-orange);
    margin-top: 0.625rem;
    z-index: 3;
}

.timeline-content {
    width: 95%;
    padding: 0 0.9375rem;
    color: #666666;
    color: var(--primary-dark-grey);
    direction: ltr;
}

.timeline-content h3 {
    margin-top: 0.3125rem;
    color: #d3443d;
    color: var(--primary-red);
    font-weight: 900;
}

/*------------ MEDIA ------------------*/
@media (max-width: 768px) {
    .experience .container {
        padding-left: 0;
    }

    .experience-wrapper::before {
        left: 0.5rem;
        margin-bottom: 1.875rem;
    }
    .timeline-block-right {
        float: none;
        width: 100%;
    }

    .timeline-block-left {
        float: none;
        direction: ltr;
        width: 100%;
    }
}

.portfolio-wrapper {
    background: #f4f4f4;
    background: var(--primary-light-grey);
    padding-bottom: 3rem;
}

.portfolio-wrapper h1 {
    color: #f9ab00;
    color: var(--primary-orange);
}

.portfolio-image {
    width: 27rem;
    border: 1px solid #f9ab00;
    border: 1px solid var(--primary-orange);
    padding: 0 0.5rem;
    height: 18rem;
}

.portfolio-image-box {
    position: relative;
    margin: 1rem;
}

.overflow {
    position: absolute;
    opacity: 0;
    background: #000000;
    background: var(--primary-black);
    width: 27rem;
    height: 18rem;
    top: 0;
    border: 1px solid #d3443d;
    border: 1px solid var(--primary-red);
    cursor: pointer;
}

.portfolio-image-box:hover .overflow {
    opacity: 60%;
    transition: 0.3s ease-in-out;
}

.portfolio-icon {
    position: absolute;
    opacity: 0;
    margin-left: -14rem;
    margin-top: 7.6rem;
    color: #f9ab00;
    color: var(--primary-orange);
    font-size: 1.5rem;
}

.portfolio-image-box:hover .portfolio-icon {
    opacity: 70%;
    transition: 0.3s ease-in-out;
    cursor: pointer;
}

.popup-title {
    margin-top: 10px;
    font-size: 18px;
    font-weight: 700;
}

.popupbox-wrapper {
    margin-top: 7rem;
}

.portfolio-image-popupbox {
    width: 45rem;
    padding: 0 0.5rem;
}

.hyper-link {
    cursor: pointer;
    color: #f9ab00;
    color: var(--primary-orange);
}

.hyper-link:hover {
    color: #d3443d;
    color: var(--primary-red);
}

/*------------ MEDIA ------------------*/
@media (max-width: 768px) {
    .portfolio-image-popupbox {
        width: 100%;
    }
}

@media (max-height: 570px) {
    .popupbox-wrapper {
        height: 100%;
    }
    .portfolio-image-popupbox {
        width: 50%;
    }
}

@media (max-height: 280px) {
    .popupbox-wrapper {
        height: 90%;
    }
    .portfolio-image-popupbox {
        width: 30%;
    }
}

.contacts {
    background: rgb(52, 58, 64);
    background: var(--primary-dark);
    padding: 3rem 0;
}

.contacts h1 {
    color: #f9ab00;
    color: var(--primary-orange);
    padding: 1.3rem 0;
    text-transform: uppercase;
}

.contacts p {
    color: #f4f4f4;
    color: var(--primary-light-grey);
    font-size: 1.2rem;
}

.contacts input,
.contacts textarea {
    width: 100%;
    box-sizing: border-box;
    background: none;
    resize: none;
    border: 0;
    border-bottom: 0.125rem solid #666666;
    border-bottom: 0.125rem solid var(--primary-dark-grey);
    border-radius: 0;
    color: #f9ab00;
    color: var(--primary-orange);
    outline: 0 !important;
    box-shadow: none !important;
    margin-bottom: 1rem;
    padding-left: 0 !important;
}

.contacts .container {
    padding: 4rem 0;
}

.contacts input:focus,
.contacts textarea:focus {
    background: transparent;
    color: #f9ab00;
    color: var(--primary-orange);
    border-bottom: 0.125rem solid #666666;
    border-bottom: 0.125rem solid var(--primary-dark-grey);
}

.contacts textarea {
    height: 12.2rem !important;
}

.contact-btn {
    margin: 1rem 0 0 0;
    width: 100%;
    background: none;
}

.line {
    position: relative;
    top: -0.8rem;
    width: 0;
    height: 0.125rem;
    background: #f9ab00;
    background: var(--primary-orange);
    display: inline-block;
}

input:focus + .line,
textarea:focus + .line {
    width: 100%;
    transition: width 0.5s ease-in-out;
}

input.form-control {
    margin: -0.3rem 0;
}

textarea.form-control {
    margin: -0.3rem 0;
}

.error-message {
    color: #ffffff;
    color: var(--primary-white);
    background: #d3443d;
    background: var(--primary-hover-red);
    text-align: left;
}

.success-message {
    font-weight: 900;
    color: green;
    color: var(--primary-green);
}

/*---------------- MEDIA -----------------------*/
@media (max-width: 768px) {
    .contacts {
        overflow-x: hidden;
    }

    .contacts .container {
        padding: 0 1rem;
    }

    .contacts p {
        padding: 0 0.4rem;
    }
}

.footer {
    background: #000000;
    background: var(--primary-black);
    color: #666666;
    color: var(--primary-dark-grey);
    padding: 3rem;
}

.footer a {
    text-decoration: none;
    color: #666666;
    color: var(--primary-dark-grey);
}

.footer a:hover {
    text-decoration: none;
    color: #f9ab00;
    color: var(--primary-orange);
    cursor: pointer;
}

.footer p {
    margin-bottom: 0;
}

